.todo-container {
  width: 500px;
  min-height: 65px;
  background-color: #1b1b20;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  border: 1px solid white;
  cursor: pointer;
}

/* Colors */

.todo-container:nth-child(3n +1) {
  background-color: #1b1b20;
}

.todo-container:nth-child(3n +2) {
  background-color: #2a2a40;
}

.todo-container:nth-child(3n +3) {
  background-color: #3b3b4f;
}

/* Text */

.todo-text {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}

/* Icon */

.todo-icon {
  width: 25px;
  height: 25px;
  margin: 5px;
}

/* Todo Completed */

.todo-container.completed {
  background-color: #008000a3;
  text-decoration: line-through;
}